.header-nav-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    position: fixed;
    top: -4px;
    right: 0;
    background-color: white;
    border: 1px rgb(238, 238, 238) solid;
    height: 40px;
    overflow: hidden;

    
}
.header-tab-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 60%;
    justify-content: center;
    align-items: flex-start;
    
}


