.number-static {
    display: flex;
    flex-direction: column;
    width: 200px;
    justify-content: flex-start;
    align-items: center;
    height: 220px;
    border: 1px antiquewhite solid;
    box-shadow: 1px 1px 2px 2px aliceblue;
    background-color:#fafafa;
    margin-top: 40px;
    padding-top: 40px;
    margin-right: 20px;
    border-radius: 10px;
    cursor: pointer;
    -moz-user-select: none;
    -webkit-user-drag: none;
}

.progress-container {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    justify-self: center;
    align-self: center;

}

.static-number {

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    font-family: 'Righteous', cursive;





}

.static-total {

    align-self: flex-end;
}

.static-current {
    font-size: xx-large;
    font-weight: 600;
    align-self: flex-end;

}



.static-title {
    display: block;
    position: relative;
    text-align: center;
    font-size: small;
    width: 100%;
    font-family: 'Vazir';
    padding-top: 10px;
}




